.check-list {
    padding-left: 25px;
    position: relative;
    cursor: pointer;
    display: block;
    margin-bottom: 0;
    &.inline {
        display: inline-block;
        margin: 0 15px 10px 0;
    }
    &.color {
        margin-bottom: .5rem;
        color: #616161;
        strong {
            font-weight: 600;
            font-size: 15px
        }
    }
    .nume {
        background: rgba(244, 244, 244, 0.8);
        border-radius: 10px;
        color: #000;
        width: 30px;
        height: 18px;
        display: inline-block;
        text-align: center;
        font-size: 13px;
        font-weight: 400;
        line-height: 19px;
        margin-left: 5px;
    }
    .checkmark {
        position: absolute;
        top: 2px;
        left: 0;
        height: 16px;
        width: 16px;
        border: 1px solid #C7C7C7;
        border-radius: 2px;
        &:after {
            content: "";
            position: absolute;
            display: none;
            left: 5px;
            top: 1px;
            width: 5px;
            height: 10px;
            border: solid var(--color-green);
            border-width: 0 2px 2px 0;
            transform: rotate(45deg);
        }
    }
    input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        display: none;
    }
    input:checked~.checkmark {
        border: 1px solid var(--color-main2);
    }
    input:checked~.checkmark:after {
        display: block;
    }
    &.number {
        padding-right: 25px;
        padding-left: 0;
        display: inline-block;
        .checkmark {
            right: 0;
            left: inherit;
        }
    }
}

label.radio {
    padding-left: 25px;
    position: relative;
    cursor: pointer;
    display: inline-block;
    margin-bottom: 10px;
    &.radio-block {
        display: block;
    }
    &.color {
        margin-bottom: .5rem;
        color: #616161;
        strong {
            font-weight: 600;
            font-size: 15px
        }
    }
    .nume {
        background: rgba(244, 244, 244, 0.8);
        border-radius: 10px;
        color: #000;
        width: 30px;
        height: 18px;
        display: inline-block;
        text-align: center;
        font-size: 13px;
        font-weight: 400;
        line-height: 19px;
        margin-left: 5px;
    }
    .checkmark {
        position: absolute;
        top: 1px;
        left: 0;
        height: 16px;
        width: 16px;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #C7C7C7;
        border-radius: 50%;
        &::before {
            content: "";
            display: none;
            width: 6px;
            height: 6px;
            background: var(--color-main);
            border-radius: 50%;
        }
    }
    input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        display: none;
    }
    input:checked~.checkmark {
        border: 1px solid var(--color-main);
    }
    input:checked~.checkmark:before {
        display: block;
    }
}

label.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 24px;
    margin-bottom: 0;
    input {
        opacity: 0;
        width: 0;
        height: 0;
    }
    .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #c7c1c1;
        -webkit-transition: .4s;
        transition: .4s;
        &:before {
            position: absolute;
            content: "";
            height: 20px;
            width: 20px;
            left: 2px;
            bottom: 2px;
            background-color: white;
            -webkit-transition: .4s;
            transition: .4s;
        }
    }
    input:checked+.slider {
        background-color: var(--color-main2);
    }
    input:focus+.slider {
        box-shadow: 0 0 1px var(--color-main2);
    }
    input:checked+.slider:before {
        -webkit-transform: translateX(35px);
        -ms-transform: translateX(35px);
        transform: translateX(35px);
    }
    .slider.round {
        border-radius: 34px;
    }
    .slider.round:before {
        border-radius: 50%;
    }
}

