    }
    .widget-header {
        background: #f9f9f9;
        border-bottom-color: #d9d9d9;
        border-bottom: 1px solid #ececec;
        line-height: 35px;
        padding: 0 12px;
        margin-bottom: 0;
        h4 {
            display: inline-block;
            color: #555555;
            font-size: 14px;
            font-weight: 400;
            margin: 0;
            padding: 0;
            i {
                color: #6f6f6f;
                margin-right: 7px;
            }
        }
    }
    .dd-list {
        padding: 10px;
        margin-bottom: 0;
        li {
            display: block;
            position: relative;
            .colspan {
                position: absolute;
                left: 0;
                background: none;
                z-index: 2;
                border: 0;
                font-size: 10px;
                height: 32px;
                width: 30px;
                &.active {
                    i::before {
                        content: "\f068";
                    }
                }
            }
            .dd-handle {
                background: #F3F3F3;
                border: 1px solid #D9D9D9;
                margin: 5px 0;
                padding: 5px 100px 5px 30px;
                display: flex;
                align-items: center;
                position: relative;
                &:hover {
                    .control {
                        opacity: 1;
                    }
                }
                label.switch {
                    right: 5px;
                    position: absolute;
                    width: 90px;
                    .slider {
                        &::after {
                            content: "Crawl Off";
                            -webkit-transition: .4s;
                            transition: .4s;
                            position: absolute;
                            right: 8px;
                            font-size: 13px;
                            top: 2px;
                            color: #fff;
                        }
                    }
                    input:checked+.slider:after {
                        content: "Crawl On";
                        right: 28px;
                    }
                    input:checked+.slider:before {
                        transform: translateX(65px);
                    }
                }
            }
            .control {
                margin-left: 10px;
                opacity: 0;
                a {
                    margin-left: 10px;
                }
            }
        }
        .lever2 {
            padding-left: 20px;
        }
        .lever3 {
            padding-left: 40px;
        }
        .lever4 {
            padding-left: 60px;
        }
    }
}

