// Boostrap custom
.tooltip.in {
    filter: alpha(opacity=90);
    opacity: .9;
}

.ava+.tooltip.in {
    left: 40px;
    .tooltip-arrow {
        display: none
    }
    .tooltip-inner {
        border: 0;
        padding: 4px;
    }
    img {
        width: 100px;
    }
}

.tooltip-img {
    position: relative;
    .tooltip-inner {
        position: absolute;
        width: 300px;
        max-width: 300px;
        background: #FFFFFF;
        padding: 4px;
        border: 0;
        left: 40px;
        top: 0px;
        z-index: -1;
        img {
            width: 100%;
        }
    }
    &:hover {
        .tooltip-inner {
            opacity: 1;
            z-index: 1001;
        }
    }
}

.fade.in {
    opacity: 1;
}

.fade {
    opacity: 0;
    -webkit-transition: opacity .15s linear;
    -o-transition: opacity .15s linear;
    transition: opacity .15s linear;
}

.tooltip-inner {
    max-width: 140px;
    text-align: left;
    border: 1px solid #14B911;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    background: #FFFFFF;
    color: #333;
    font-size: 12px;
}

.tooltip-arrow {
    position: absolute;
    width: 0;
    height: 0;
    border-color: transparent;
    border-style: solid;
}

.tooltip.right {
    padding: 0 5px;
    margin-left: 3px;
    min-width: 140px;
}

.tooltip.right .tooltip-arrow {
    top: 50%;
    left: 0;
    margin-top: -5px;
    border-width: 5px 5px 5px 0;
    border-right-color: #14B911;
}

.tooltip.top {
    margin-bottom: 3px;
    padding: 5px 0;
}

.tooltip.top .tooltip-arrow {
    bottom: 0;
    left: 0;
    border-width: 5px 5px 0px 5px;
    border-top-color: #14B911;
}

.page-item.active .page-link {
    background-color: var(--color-main);
    border-color: var(--color-main);
}

.page-link {
    color: var(--color-main);
}

