.main-container {
    padding-top: 20px;
    margin-bottom: 80px;
}

.title_cate {
    color: #3F3F3F;
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 15px;
    font-size: 14px;
}

.thong_ke_top {
    [class *='col-'] {
        display: flex;
        align-items: center;
        border-right: 1px solid rgba(225, 225, 225, 0.6);
        .icon {
            margin-right: 8px;
            img {
                border-radius: 50%;
            }
        }
        .content {
            line-height: 1.2;
            .txt_22 {
                font-size: 20px;
                margin: 5px 0;
            }
        }
        &:last-child {
            border-right: 0
        }
    }
    @media (max-width: 1400px) {
        .row {
            margin: 0 -8px;
        }
        [class *='col-'] {
            padding: 0 8px;
        }
    }
    @media (max-width: 1200px) {
        [class *='col-'] {
            flex-wrap: wrap;
            .icon {
                width: 100%;
                margin: 0 0 5px 0;
            }
        }
        .txt_15 {
            font-size: 13px;
        }
    }
    @media (max-width: 1025px) {
        [class *='col-'] {
            flex: 0 0 33.33%;
            max-width: 33.33%;
            margin: 6px 0;
            &:nth-child(3) {
                border-right: 0;
            }
        }
    }
    @media (max-width: 991px) {
        [class *='col-'] {
            flex: 0 0 20%;
            max-width: 20%;
            &:nth-child(3) {
                border-right: 1px solid rgba(225, 225, 225, 0.6);
            }
        }
    }
    @media (max-width: 576px) {
        [class *='col-'] {
            flex-wrap: none;
            flex: 0 0 50%;
            max-width: 50%;
            .icon {
                width: 30px;
            }
            &:nth-child(2),
            &:nth-child(4) {
                border-right: 0;
            }
        }
    }
}

.flex-head {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    .title_cate {
        margin-right: 30px;
    }
    .td2 {
        margin-bottom: 15px;
        font-weight: 500;
        font-size: 14px;
        i {
            color: #999;
            margin-right: 3px;
        }
    }
    .btn-right {
        flex-grow: 3;
        .btn {
            background: #0059A1;
            border-radius: 3px;
            margin-bottom: 15px;
            color: #fff;
            font-weight: bold;
        }
    }
}

.tab-default {
    margin-bottom: 15px;
    display: flex;
    align-items: center;
    a {
        display: inline-block;
        color: #000000;
        padding: 10px;
        &.active {
            border-bottom: 1px solid #056BD3;
            color: #056BD3
        }
    }
    .dropdown {
        padding: 10px;
        .dropbtn {
            color: #000000;
        }
    }
    &.block {
        flex-wrap: wrap;
        a {
            display: block;
            width: 100%;
            border-bottom: 1px solid #E1E1E1;
            font-size: 13px;
            border-left: 0;
            padding: 14px 30px 14px 10px;
            position: relative;
            i {
                vertical-align: text-bottom;
                margin-right: 5px;
            }
            .fa-chevron-right {
                position: absolute;
                margin: 0;
                right: 10px;
                top: 19px;
            }
            &.active {
                border-left: 1px solid #0059A1;
                color: #0059A1;
                font-weight: bold
            }
        }
    }
    @media (max-width: 600px) {
        flex-wrap: wrap;
        // display: block;
        // white-space: nowrap;
        // overflow: auto;
        // -webkit-overflow-scrolling: touch;
        // -ms-overflow-style: -ms-autohiding-scrollbar;
        // &::-webkit-scrollbar {
        //     display: none;
        // }
    }
}

.title_page {
    font-size: 20px;
    margin-bottom: 10px;
    font-weight: bold
}

.line-bottom {
    border-bottom: 1px solid #E1E1E1;
    padding-bottom: 10px;
    display: block;
    margin-bottom: 15px;
}

.line-left,
.line-left-pc {
    border-left: 1px solid #e5e5e5;
}

.line-left-pc {
    @media (max-width: 767px) {
        border-left: 0
    }
}

.line-color {
    color: #056BD3;
    text-decoration: underline;
}

.row-main {
    &-left {
        width: 900px;
        width: calc(100% - 350px);
        padding-right: 15px;
    }
    &-right {
        width: 350px;
    }
    &_col200 {
        width: 280px;
        margin-bottom: 30px;
        .title_page {
            margin-bottom: 20px;
        }
        .wrap_bg2 {
            padding: 20px 10px;
        }
        .title-margin {
            background: #0059A1;
            color: #fff;
            font-size: 14px;
            padding: 7px 15px;
            position: relative;
            margin: -30px 0 10px -22px;
            &::before {
                content: "";
                border-top: 0px solid transparent;
                border-bottom: 12px solid transparent;
                border-right: 12px solid rgba(0, 89, 161, 0.83);
                position: absolute;
                left: 0;
                bottom: -12px;
            }
            .fa {
                color: #fff;
                position: absolute;
                right: 10px;
            }
        }
        .tag {
            display: flex;
            flex-wrap: wrap;
            margin-bottom: 15px;
            border: 1px solid #CCCCCC;
            border-radius: 3px;
            padding: 6px 6px 0 6px;
            a {
                background: #CCCACA;
                display: inline-block;
                padding: 5px 8px;
                margin: 0 6px 6px 0;
                color: #000000;
            }
        }
        .form-group {
            margin-bottom: 12px;
            .form-control {
                border: 1px solid #CCCCCC;
                border-radius: 3px;
                font-size: 14px;
                height: 30px;
                padding: 0 10px;
                margin: 0;
                @include placeholder {
                    color: #ADB0B1;
                }
            }
        }
        .search-wrapper {
            .input_search {
                border-bottom: 1px solid #BABABA;
                border-radius: 0;
                font-size: 14px;
                padding-left: 28px;
            }
            .search-btn {
                width: 30px;
            }
        }
        .ul-cate {
            li {
                display: block;
                position: relative;
                &>a {
                    color: #111111;
                    display: block;
                    border-bottom: 1px solid #BABABA;
                    padding: 10px 20px 10px 28px;
                }
                .collapsed {
                    .check-list {
                        width: 16px;
                        height: 16px;
                        display: inline-block;
                        padding: 0;
                        margin-right: 4px;
                    }
                }
                .lever2 {
                    padding-left: 22px;
                }
                &.all {
                    a {
                        background: #F2F8E8;
                    }
                    .check-list {
                        padding: 10px 20px 10px 28px;
                        border-bottom: 1px solid #BABABA;
                        .checkmark {
                            top: 11px;
                            left: 5px;
                        }
                    }
                }
                &.lever1 {
                    .onclick-togle,
                    .collapsed {
                        &::before {
                            content: "+";
                            position: absolute;
                            left: 10px;
                            top: 8px;
                            color: #7B7B7B;
                        }
                        &.active {
                            &::before {
                                content: "-";
                                top: 9px;
                            }
                        }
                    }
                }
                .lever2 {
                    li {
                        a {
                            padding-left: 40px;
                        }
                        &:hover {
                            a {
                                background: #F3F3F3;
                            }
                        }
                    }
                    .control {
                        position: absolute;
                        right: 10px;
                        top: 7px;
                        a {
                            padding-left: 0;
                            margin-left: 5px;
                        }
                    }
                }
            }
        }
    }
    &_col800 {
        width: 950px;
        width: calc(100% - 280px);
        padding-left: 20px;
    }
}

.close-menu {
    position: absolute;
    right: 15px;
    font-size: 25px;
    top: -5px;
    display: none;
    z-index: 2;
}

.btn_show {
    font-weight: 500;
    margin-bottom: 15px;
    display: none;
    margin-bottom: 10px;
}

body.show-menu {
    .row-main_col200 {
        transform: translate(0%);
        background: #fff;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.2);
        padding: 25px 10px 0 10px;
        overflow-y: scroll;
        margin-bottom: 0;
        .title_page {
            padding-left: 15px;
        }
    }
}

.list-notifi {
    position: relative;
    &::before {
        content: "";
        background: #D9D9D9;
        top: 10px;
        bottom: 0;
        width: 1px;
        position: absolute;
        left: 11px;
    }
    li {
        position: relative;
        font-size: 14px;
        margin-bottom: 20px;
        padding-left: 28px;
        i {
            position: absolute;
            left: 0;
            width: 20px;
            top: 2px;
            height: 20px;
            text-align: center;
            line-height: 20px;
            font-size: 11px;
            border-radius: 50%;
            background: #5EC6F2;
            color: #fff;
        }
        .note {
            font-size: 12px;
            font-style: italic;
            position: relative;
            padding-right: 40px;
            .feeling {
                position: absolute;
                top: 0;
                right: 10px
            }
            p {
                margin-bottom: 0;
            }
        }
        .date {
            font-size: 12px;
            color: var(--gray100);
        }
        .icon {
            position: absolute;
            left: 0;
            width: 24px;
            top: 2px;
            height: 24px;
            border-radius: 50%;
            &--v2 {
                border: 1px solid #0059A1;
                width: 24px;
                height: 24px;
                background: #0059A1;
                padding: 0px 2px;
                text-align: center;
                img {
                    height: 14px;
                }
            }
        }
    }
    .list-kerry {
        ul {
            position: relative;
            &::before {
                content: "";
                background: #D9D9D9;
                top: 6px;
                bottom: 0;
                width: 1px;
                position: absolute;
                left: 11px;
            }
            li {
                margin-top: 5px;
                margin-bottom: 0;
                display: flex;
                position: relative;
                &::before {
                    content: "";
                    position: absolute;
                    left: 6px;
                    top: 5px;
                    border: 1px solid #FA0B0B;
                    background: #fff;
                    width: 10px;
                    height: 10px;
                    border-radius: 50%;
                }
                .name {
                    width: 55%;
                    color: #FA0B0B;
                }
                .date {
                    width: 45%;
                    text-align: right;
                    padding-left: 15px;
                }
                .nv {
                    color: var(--gray100);
                }
                &.active {
                    &::before {
                        background: #FA0B0B;
                    }
                }
            }
        }
    }
    &.scrollbar-inner {
        margin-right: -10px;
        padding-right: 10px;
        @media (min-width: 992px) {
            height: calc(100vh - 620px);
        }
    }
}

.hoat_dong {
    .list-notifi {
        height: 300px;
    }
}

.search-wrapper {
    position: relative;
    .btn_reset {
        position: absolute;
        right: 32px;
        top: 7px;
        width: 20px;
        height: 20px;
        border: 0;
        outline: 0;
        border-radius: 50%;
        cursor: pointer;
        background: #C4C4C4;
    }
}

.input-relative {
    position: relative;
    input {
        padding-right: 30px !important;
    }
    .btn_reset {
        position: absolute;
        right: 5px;
        top: 5px;
        width: 20px;
        height: 20px;
        border: 0;
        outline: 0;
        border-radius: 50%;
        cursor: pointer;
        background: #C4C4C4;
    }
}

.row-main-head {
    align-items: center;
    .search-wrapper {
        max-width: 480px;
        width: 100%;
        margin-bottom: 10px;
    }
    .btn-right {
        flex-grow: 3;
        margin-bottom: 10px;
        margin-right: -10px;
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-end;
        align-items: center;
        .number-new {
            position: relative;
            padding-left: 30px;
            padding-right: 10px;
            &::before {
                content: "";
                background: #BABABA;
                width: 1px;
                height: 30px;
                position: absolute;
                left: 10px;
                top: 5px;
            }
        }
        .btn {
            margin-right: 10px;
            @media (max-width: 1025px) {
                margin-bottom: 10px;
            }
        }
        @media (max-width: 991px) {
            text-align: left !important;
        }
    }
}

.down-up {
    display: inline-block;
    position: relative;
    .onclick {
        &:focus,
        &.active {
            border-radius: 0;
        }
    }
    .form-down {
        background: #fff;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, .2);
        float: left;
        position: absolute;
        text-align: left;
        z-index: 2;
        width: 180px;
        right: 0;
        padding: 10px;
        font-size: 13px;
        display: none;
        .check-list {
            display: inline-block;
            width: 100%;
            font-size: 13px;
            margin-bottom: 5px!important;
            .checkmark {
                background: #fff;
            }
            &:last-child {
                margin-bottom: 0!important;
            }
        }
        &.col1 {
            width: 150px;
            padding: 10px;
            .check-list {
                width: 100%;
            }
        }
    }
    @media (max-width: 480px) {
        .form-down {
            width: 190px;
            .check-list {
                width: 100%;
            }
        }
    }
}

.onclick-toggle {
    position: relative;
    .content-toggle {
        background: #fff;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, .2);
        float: left;
        position: absolute;
        text-align: left;
        z-index: 2;
        width: 300px;
        right: 0;
        padding: 10px 20px;
        display: none;
    }
    .toggle.active {
        &+.content-toggle {
            display: block;
        }
    }
}

.bg-serach-main {
    background: #FFFFFF;
    border: 1px solid #ccc;
    padding: 20px 20px 10px 20px;
    margin-bottom: 20px;
    .search-wrapper {
        input {
            border: 1px solid #CCCCCC;
        }
    }
}

.detail_item {
    .title-cate {
        font-weight: bold;
        font-size: 14px;
        background: #ECECEE;
        border: 1px solid #BABABA;
        box-sizing: border-box;
        border-radius: 5px 5px 0px 0px;
        height: 40px;
        line-height: 40px;
        margin: 0;
        padding: 0 15px;
        &.flex {
            padding: 6px 10px;
            height: 100%;
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: space-between;
        }
        .btn-right {
            .btn {
                margin-right: 6px;
            }
            .delete {
                margin-left: 15px;
            }
            .add-product {
                display: inherit;
                position: relative;
                margin-bottom: 5px;
                ul {
                    position: absolute;
                    top: 60px;
                    background: #fff;
                    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.35);
                    padding: 0 15px;
                    min-width: 200px;
                    border-radius: 4px;
                    opacity: 0;
                    visibility: hidden;
                    transition-duration: 200ms;
                    transition-property: opacity, top, visible;
                    transition-timing-function: cubic-bezier(.7, 1, .7, 1);
                    li {
                        line-height: 1.5;
                        font-weight: 400;
                        padding: 8px 0;
                    }
                }
                &:hover {
                    ul {
                        opacity: 1;
                        visibility: visible;
                        top: 32px;
                    }
                }
            }
            @media (max-width: 590px) {
                .delete {
                    margin-left: 0;
                }
            }
        }
    }
    .warp_padding20 {
        background: #fff;
        padding: 15px;
        .input_search {
            border: 1px solid #ccc;
        }
    }
    .form_donhang {
        .col-md-6 {
            padding: 0 50px;
        }
        .form-group {
            .lbl {
                text-align: right;
                width: 200px;
                padding-right: 20px;
            }
            .wrap_input,
            .datepicker-wrap {
                width: calc(100% - 200px);
            }
        }
        @media (max-width: 767px) {
            .col-md-6 {
                padding: 0 15px;
            }
            .form-group {
                .lbl {
                    width: 160px;
                    padding-right: 15px;
                }
                .wrap_input,
                .datepicker-wrap {
                    width: calc(100% - 160px);
                }
            }
        }
        @media (max-width: 576px) {
            .col-md-6 {
                padding: 0 15px;
            }
            .form-group {
                .lbl {
                    width: 100%;
                    padding-right: 0;
                    text-align: left;
                    margin-bottom: 5px;
                }
                .wrap_input,
                .datepicker-wrap {
                    width: 100%;
                }
            }
        }
    }
}

.thongtin_dinhkem {
