.scroll-popup {
    max-height: 70vh;
    overflow-y: auto;
    overflow-x: hidden;
    padding-right: 10px;
    margin-right: -10px;
    &::-webkit-scrollbar {
        width: 5px;
    }
    &::-webkit-scrollbar-thumb {
        background-color: #E1E1E1;
        width: 5px;
        border-radius: 10px;
    }
}

.scroll-inner {
    max-height: 300px;
    overflow-y: auto;
    overflow-x: hidden;
    scrollbar-color: #E1E1E1 #fff;
    scrollbar-width: thin;
    padding-right: 10px;
    margin-right: -10px;
    &::-webkit-scrollbar {
        width: 5px;
    }
    &::-webkit-scrollbar-thumb {
        background-color: #E1E1E1;
        width: 5px;
        border-radius: 10px;
    }
}

.table-responsive.scroll-inner {
    max-height: 60vh;
    table {
        th {
            position: sticky;
            top: -1px;
            z-index: 1;
        }
    }
}

