.multiple-select {
    position: relative;
    max-width: 100%;
    width: 200px;
    * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
    }
    .select-btn {
        display: flex;
        height: 35px;
        align-items: center;
        justify-content: space-between;
        padding: 0 6px;
        border-radius: .25rem;
        cursor: pointer;
        background-color: #fff;
        border: 1px solid #ccc;
        .btn-text {
            font-size: 13px;
            font-weight: 400;
            color: #333;
        }
        .arrow-dwn {
            display: flex;
            height: 20px;
            width: 20px;
            color: #fff;
            font-size: 13px;
            border-radius: 50%;
            background: var(--color-green);
            align-items: center;
            justify-content: center;
            transition: 0.3s;
        }
        &.open {
            .arrow-dwn {
                transform: rotate(-180deg);
            }
        }
    }
    .list-items {
        position: absolute;
        width: 100%;
        z-index: 2;
        margin-top: 5px;
        padding: 5px;
        background-color: #fff;
        box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
        display: none;
    }
    .select-btn.open~.list-items {
        display: block;
    }
    .list-items {
        .item {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            list-style: none;
            cursor: pointer;
            transition: 0.3s;
            padding: 5px;
            border-radius: 5px;
            position: relative;
            &:hover {
                background-color: #e7edfe;
            }
            .item-text {
                font-size: 13px;
                font-weight: 400;
                color: #333;
            }
            .checkbox {
                display: flex;
                align-items: center;
                justify-content: center;
                height: 16px;
                width: 16px;
                border-radius: 4px;
                margin-right: 6px;
                border: 1.5px solid #c0c0c0;
                transition: all 0.3s ease-in-out;
                .fa-check {
                    color: #fff;
                    font-size: 11px;
                    transform: scale(0);
                    transition: all 0.2s ease-in-out;
                }
            }
            &.checked {
                .checkbox {
                    background-color: #4070f4;
                    border-color: #4070f4;
                }
                .fa-check {
                    transform: scale(1);
                    color: #fff;
                }
            }
            .btn-expend {
                position: absolute;
                top: 3px;
                right: 0px;
                z-index: 1;
                width: 26px;
                height: 20px;
                text-align: center;
                font-size: 15px;
                cursor: pointer;
                &::before {
                    content: "+";
                    color: var(--color-green);
                    font-weight: bold;
                }
            }
            .get-item {
                display: flex;
                flex-wrap: wrap;
                width: 90%;
                width: calc(100% - 30px);
                align-items: center;
            }
            .level2 {
                width: 100%;
                padding-left: 22px;
                li {
                    display: flex;
                    align-items: center;
                    margin-top: 8px;
                    .item-text {
                        font-size: 11px;
                    }
                }
            }
        }
    }
}

.flex-form-group {
    display: flex;
    .flex-text {
        min-width: 130px;
    }
    .flex-input {
        width: 100%;
    }
    @media (max-width: 600px) {
        flex-wrap: wrap;
        .flex-text {
            min-width: 100%;
        }
        .flex-input {
            width: 100%;
        }
    }
}